import Hero from '@/components/Hero';

export default async function Home() {
  const url = `${process.env.NEXT_PUBLIC_STRAPI_URL}/api/home?populate[secciones][populate]=*`;
  
  try {
    const req = await fetch(url, { cache: 'no-store' });
    const response = await req.json();

    // ESTO ES PARA DEPURAR: Mira tu terminal de VS Code después de guardar
    console.log("Datos de Strapi:", JSON.stringify(response, null, 2));

    // Strapi 5 entrega los datos directamente en response.data
    const data = response.data;
    
    // Verificamos si secciones existe antes de buscar
    const secciones = data?.secciones || [];
    const heroData = secciones.find((s: any) => s.__component === "sections.hero");

    if (!heroData) {
      return <div className="p-10 text-center">No se encontró el Hero en Strapi. Revisa el log de la terminal.</div>;
    }

    return (
   <main>
    <pre className="bg-white text-black p-20">
      {JSON.stringify(heroData, null, 2)}
    </pre>
  </main>
    );
  } catch (error) {
    console.error("Error cargando Strapi:", error);
    return <div className="p-10 text-red-500">Error de conexión con el servidor.</div>;
  }
}